	module.exports = {"data":{"markdownRemark":{"html":"<p>例によってコミケ当選しました！今年の冬コミは<em>木曜日 東メ-06a</em>で無事当選いたしました ,,＞◡＜,,</p>\n<p>というわけで出し物の紹介をいたします。</p>\n<h2><a href=\"\">NEW!</a>(/answer-the-conclusion) 500 円</h2>\n<p>今回の新刊は東方のラノベです！</p>\n<p>東方夢終劇 10周年記念ということで、↓を題材としたスピンオフ物語を作成しました！</p>\n<h2><a href=\"\">既刊ゲーム</a>(/!/thC) 1000 円</h2>\n<p>夢終劇は今回が最終再販版です。</p>\n<p>以後の販売はありませんので、お持ちでない方は今回お越しいただければ幸いです。</p>\n<p><a href=\"http://www.toranoana.jp/mailorder/article/04/0030/38/09/040030380928.html\">とらのあなの委託もまだあります</a>が、残数が少ないためこちらもお急ぎください。</p>\n<p>※プレイヤーさんのリプレイです。</p>\n<h2><a href=\"\">既刊ゲーム</a>(<a href=\"http://kagaminer.in/\">http://kagaminer.in/</a>) 1,000 円</h2>\n<p>※プレイヤーさんのリプレイです。</p>\n<p><a href=\"http://dangoya.moo.jp/\">DANGOYA</a> さんとの合作、東方の世界観を使った縦型弾幕 STG です。\nチルノや諏訪子などおなじみのキャラが幻想郷のようで幻想郷でない、謎めいた世界に迷い込んでしまったが、そんなこともお構いなしに大暴れ！オリキャラも登場します！</p>\n<p>Windows 用 (XP SP3以降必須) です。</p>\n<h2><a href=\"\">既刊ラノベ</a>(/evilmagic) <del>800 円</del>→500 円 (コミケ特別価格)</h2>\n<p><img src=\"https://danmaq.com/wp-content/uploads/2012/11/em_POP.png\"></p>\n<p>180 ページの本格ファンタジーなライトノベルです。</p>\n<p>不思議な能力が使える女の子エナと、弟と幼馴染の悪ガキトリオが大暴れ！\nしかしもっと悪いヤツ、領主は幼馴染の男の子を攫ってしまった！\n事を知って怒髪天なエナは領主に勝負に挑むが……！？</p>\n<p><a href=\"/filez/em_trial.pdf\">気になる方は冒頭 69 ページだけ読める体験版をどうぞ！</a> (4MB | PDF 注意)</p>\n<h2><a href=\"\">既刊音楽CD</a>(/!/leila/) <del>300 円</del>→100 円 (コミケ特別価格)</h2>\n<p><img src=\"https://danmaq.com/wp-content/uploads/2012/11/leila1-300x296.jpg\"></p>\n<p>13 曲 64 分の東方アレンジ音楽 CD です。\n秘封倶楽部な楽曲を中心に、シンセサイザ KORG OASYS でリミックスしてみました！</p>\n<ul>\n<li><a href=\"/!/leila/x.mp3\">全曲クロスフェード デモを聴く</a> (7:11 | 8MB)</li>\n</ul>\n<p>なお、上記の告知は<em>すべて予定</em>です。\n当日までに頒布物が追加・変更になる可能性がありますのでご了承ください。</p>","frontmatter":{"cover":null,"date":"2016-12-25T19:24:19+09:00","strDate":"2016/12/25","title":"コミックマーケット91参加情報[木曜日 東メ-06a]"},"fields":{"langKey":"ja","slug":"/ja/blog/2016/12/25-c91/"}}},"pathContext":{"path":"/ja/blog/2016/12/25-c91/","langKey":"ja"}}

